@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --font-family: "Poppins", sans-serif;
  --dark-blue: #0a1930;
  --light-blue: #1f93ff;

  --color-white: #fff;
  --dark-text: #333;
  --form-button: #2da5ff;
  --color-grey: #eae6eb;
  --box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  font-size: 10px;
}

body {
  font-family: "Poppins", sans-serif;
  font-family: var(--font-family);
}

section {
  width: 100%;
  padding: 8rem 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}


/* Text styles */
.u-text-small {
    font-size: 1.3rem;
    line-height: 1.4;
    font-weight: 500;
  }
  
  .u-text-dark {
    color: var(--dark-text);
  }
  .u-text-light {
    color: var(--color-white);
  }
  
  .u-text-center {
    text-align: center;
  }
  
  /* Title */
  .u-title {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  .u-title h2 {
    font-size: 2.5rem;
    font-weight: 300;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--dark-text);
    margin: 1.5rem 0;
  }
  
  .u-title p {
    width: 50%;
    min-width: 40rem;
    margin: 0 auto;
    color: var(--dark-text);
  }
.header-bg {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background: var(--dark-blue);
    padding-bottom: 2rem;
}
section#download {
    background: linear-gradient(
      90deg,
      rgba(45, 165, 255, 1) 45%,
      rgba(1, 223, 245, 1) 100%
    );
    text-align: center;
    color: #fff;
  }
  
  .download h2 {
    font-size: 3rem;
    font-weight: 200;
    line-height: 1.4;
    text-transform: uppercase;
    margin: 1.5rem 0;
    position: relative;
    padding-bottom: 1rem;
  }
  
  .download h2::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 10%;
    height: 1px;
    background: var(--color-white);
    transform: translateX(-50%);
  }
  .download-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem;
  }
  
  .download-icon {
    margin: 1rem 2rem;
    border: 2px solid #fff;
    border-radius: 5px;
    width: 12rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    font-size: 1.5rem;
  }

.question {
    border: 1px solid var(--light-blue);
    border-radius: 3px;
    width: 90%;
    max-width: 60rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    box-shadow: var(--box-shadow);
  }

  .question-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    /* padding-bottom: 1rem; */
  }
  
  .question-title h4 {
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 1.4;
    text-transform: uppercase;
  }
  
  button.question-icons {
    border: 1px solid var(--color-grey);
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .features-content {
    display: flex;
    justify-content: space-between;

    /* padding: 3rem 0; */
  }
  
  .features-left {
    flex: 1 1;
    text-align: center;
  }
  .features-left img {
    width: 90%;
  }
  .features-right {
    flex: 1 1;
  }
  
  @media screen and (max-width: 600px) {
    .features-content {
      flex-direction: column;
    }
  }
.feature {
    display: flex;
    align-items: flex-start;
    margin-bottom: 2rem;
  }
  
  .feature-icon {
    position: relative;
    margin-right: 2rem;
  }
  
  .inner-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .feature-text h3 {
    font-size: 2rem;
    font-weight: 200;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--light-blue);
  }
footer#footer {
    background: var(--dark-blue);
    color: #fff;
    width: 100%;
    padding: 8rem 0;
  }
  
  .footer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    grid-gap: 2rem;
    grid-column-gap: 2rem;
    column-gap: 2rem;
  }
  
  /* .footer > * {
    flex: 1;
    justify-self: center;
  } */
  
  .footer h4 {
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    margin-bottom: 1rem;
  }
  
  .footer-box .footer-links a {
    font-size: 1.5rem;
    color: var(--color-white);
    text-decoration: none;
  }

  .footer-links {
    display: flex;
    flex-direction: column;
  }
  
  .footer-contact p {
    display: flex;
    font-size: 1.3rem;
    font-weight: 500;
    align-items: center;
    margin-bottom: 5px;
  }
#header {
    position: relative;
  }
  .header {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }

  .header h1 {
    font-size: 4rem;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--color-white);
    position: relative;
    padding-bottom: 2rem;
  }

  .header h1 span {
    display: block;
  }

  h1 span:nth-child(1) {
    font-weight: 800;
  }
  
  h1 span:nth-child(2),
  h1 span:nth-child(3) {
    font-weight: 100;
  }

  .header h1::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 65%;
    height: 2px;
    background: var(--color-white);
  }

  .header-left p {
    margin: 3rem 0;
    color: var(--color-white);
    width: 70%;
  }

  .header-right img {
    width: 90%;
    height: 50%;
  }

  .floating-icon {
    background-color: var(--light-blue);
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    cursor: pointer;
  }

  .floating-icon a {
    border: 1px solid #fff;
    color: var(--color-white);
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .mouse {
    transform: translateY(-25%);
    animation: bounce 0.8s ease infinite alternate;
  }

  @keyframes bounce {
    0% {
      transform: translateY(-25%);
    }
    100% {
      transform: translateY(25%);
    }
  }

  @media screen and (max-width: 769px) {
    .header h1 {
      font-size: 3rem;
    }
  }
  
  @media screen and (max-width: 600px) {
    .header {
      flex-direction: column;
    }

    .header-left,
    .header-right {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      margin: 0 auto;
    }

    .header-left {
      margin-bottom: 4rem;
    }

    .header h1::after {
      left: 50%;
      transform: translateX(-50%);
    }
    .header-right img {
      width: 80%;
    }
  }
a.btn {
    border-radius: 5px;
    padding: 5px 8px;
    margin-left: 1rem;
    font-weight: 400;
    font-size: 1.6rem;
    font-weight: 400;
    color: var(--color-white);
    text-decoration: none;
    margin-right: 1rem;
    transition: all 0.3s;
  }


  a.btn-dark {
    background: transparent;
    border: 2px solid var(--color-white);
  }
  a.btn-orange {
    background:orangered;
    border: 2px solid transparent;
    color: var(--color-white);
  }

  a.btn-dark:hover {
    color: orangered;
    font-weight: 400;
  }
  a.btn-orange:hover {
    color: var(--color-blue);
    font-weight: 400;
  }

  @media screen and (max-width: 600px) {
    a.btn {
      display: inline;
    }
  }
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8rem;
  }

  .logo {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .logo-text {
    margin-left: .8rem;
    font-size: 4rem;
    font-weight: 400;
    color: var(--color-white);
  }
  
  .logo-text span {
    font-weight: 600;
    font-size: 6rem;
    color: orangered;
  }
  
  .nav-links {
    display: flex;
    color: var(--color-white);
  }

  .nav-links li {
    margin-left: 1rem;
    list-style: none;
  }

  .nav-links a {
    font-size: 1.6rem;
    font-weight: 300;
    color: var(--color-white);
    text-decoration: none;
    margin-right: 1rem;
    transition: all 0.3s;
  }
  .nav-links a:hover,
  #nav-links-mobile a:hover {
    color: orangered;
  }

  .navbar .menu-icons {
    background: transparent;
    border: none;
    display: none;
  }

  /* Media Queries */
  @media screen and (max-width: 769px) {
    /* hide nav button on navbar */
    .nav-btn {
      display: none;
    }
  }
  @media screen and (max-width: 600px) {
    /* show nav button on navbar menu */
    .nav-btn {
      display: block;
    }
  
    .nav-links {
      display: none;
      border: 2px solid var(--color-white);
      z-index: 111;
    }
  
    .navbar .menu-icons {
      display: block;
    }
  
    .navbar #nav-links-mobile,
    .navbar #nav-links-mobile-hide {
      display: block;
      position: absolute;
      list-style: none;
      background: #0a1930;
      left: 0;
      top: 80px;
      width: 100%;
      transition: all 0.5s;
      border-top: 1px solid var(--color-white);
      padding-bottom: 5px;
    }

    .navbar #nav-links-mobile {
      animation: slide-in 0.5s ease-in-out;
    }
  
    .navbar #nav-links-mobile-hide {
      left: -100%;
      animation: slide-out 0.5s ease-in-out;
    }
    @keyframes slide-out {
      0% {
        transform: translateX(0);
      }
      100% {
        transform: translateX(-100%);
      }
    }
  
    @keyframes slide-in {
      0% {
        transform: translateX(-100%);
      }
      100% {
        transform: translateX(0);
      }
    }
  
    .navbar #nav-links-mobile li,
    .navbar #nav-links-mobile-hide li {
      text-align: center;
      padding: 10px 0;
      border-bottom: 1px solid var(--color-white);
      margin: 0 3rem;
    }
  
    .navbar #nav-links-mobile li:last-child,
    .navbar #nav-links-mobile-hide li:last-child {
      border-bottom: none;
    }
  
    .navbar #nav-links-mobile li a {
      color: var(--color-white);
      text-decoration: none;
      font-size: 1.8rem;
    }
  
    .navbar #nav-links-mobile a:hover {
      color: orangered;
    }
  }
section#subscribe {
    background-image:linear-gradient(rgba(0, 0, 0, 0.8),rgba(0, 0, 0, 0.8)) , url(/OSonic-FunApp/static/media/bgsonic.b29edfa9.webp);
    text-align: center;
    color: orangered;
    background-size: cover;
    /* background-attachment: fixed; */
    opacity: .9;
  }

  .subscribe h2 {
    font-size: 3.5rem;
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    margin: 1.5rem 0;
    padding-bottom: 1rem;
  }

  form > * {
    width: 100%;
    max-width: 50rem;
    margin: 3rem auto;
  }
  
  form input {
    padding: 1rem;
    background: transparent;
    border: 2px solid orangered;
    border-radius: 5px 0 0 5px;
    color:  var(--color-white);
    outline: none;
    width: 80%;
    font-size: 1.6rem;
  }

  :-ms-input-placeholder {
    color: var(--color-white);
    opacity: 1; /* Firefox */
  }

  ::placeholder {
    color: var(--color-white);
    opacity: 1; /* Firefox */
  }

  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: var(--color-white);
   }
   
   ::-ms-input-placeholder { /* Microsoft Edge */
    color: var(--color-white);
   }

  form button {
    padding: 1rem;
    background:  #030338;
    border: 3px solid #030338;
    border-radius: 0 5px 5px 0;
    color: #fff;
    font-size: 1.6rem;
    font-weight: 500;
    cursor: pointer;
  }
  
  .social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .social-icon {
    margin: 2rem;
    padding: 1rem;
    width: 4rem;
    height: 4rem;
    border: 3px solid var(--dark-blue);;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .social-icon:hover {
    background: var(--light-blue);
  }
  
  @media screen and (max-width: 600px) {
    form input {
      width: 60%;
    }
  }

